.question {
  padding-top: vw(111);

  &__text {
    width: vw(804);
    margin: 0 auto vw(77);
    font-size: vw(48);
    line-height: 1.16;
    color: $white;
    font-weight: 500;
    text-align: center;
  }

  &__buttons {
    display: flex;
    justify-content: space-between;
    width: vw(610);
    margin: 0 auto;
  }

  &__btn {
    display: block;
    width: vw(296);
    padding: vw(18) vw(10) vw(9);

    font-size: vw(36);
    line-height: 1.16;
    text-align: center;
    font-weight: 500;
    color: $color-dark;
    text-transform: uppercase;

    box-shadow: inset vw(15) vw(16) vw(26) rgba(0, 0, 0, 0.48);
    border-radius: vw(21);

    &--true {
      background: $color-turquoise;
    }

    &--myth {
      color: $white;
      background-color: $red;
    }
  }
}
